<template>
	<view class="cart-container" v-if="cart.length !== 0">
		 <my-address></my-address>
		<view class="cart-title">
		  <!-- 左侧的图标 -->
		  <uni-icons type="shop" size="18"></uni-icons>
		  <!-- 描述文本 -->
		  <text class="cart-title-text">购物车</text>
		</view>
		<view class="">
			<uni-swipe-action>
			  <block v-for="(goods, i) in cart" :key="i">
			    <!-- uni-swipe-action-item 可以为其子节点提供滑动操作的效果。需要通过 options 属性来指定操作按钮的配置信息 -->
			    <uni-swipe-action-item :right-options="options" @click="swipeActionClickHandler(goods)">
			      <my-goods :goods="goods" :show-radio="true" :show-num="true" @radio-change="radioChangeHandler" @num-change="numberChangeHandler"></my-goods>
			    </uni-swipe-action-item>
			  </block>
			</uni-swipe-action>
		</view>
		<!-- <block v-for="(goods, i) in cart" :key="i">
		  <my-goods :goods="goods"></my-goods>
		</block> -->
		 <my-settle></my-settle>
	</view>
	<view class="empty-cart" v-else>
	    <!-- <image src="/static/imge/webp.gif" class="empty-img"></image> -->
	    <text class="tip-text">空空如也~</text>
	  </view>
	
</template>

<script>
import { mapGetters ,mapState ,mapMutations} from 'vuex'
import badgeMix from '@/mixins/tabbar-badge.js'
import myGoods from '@/components/myGoods/myGoods.vue';
import mySettle from '@/components/mySettle/mySettle.vue';
import myAddress from '@/components/myAddress/myAddress.vue';
export default {
	components:{
		myGoods,
		mySettle,
		myAddress,
	},
  data() {
    return {
		  options: [{
		      text: '删除', // 显示的文本内容
		      style: {
		        backgroundColor: '#C00000' // 按钮的背景颜色
		      }
		    }],
	}
  },
   mixins: [badgeMix],
  computed: {
    // 将 m_cart 模块中的 total 映射为当前页面的计算属性
    ...mapGetters('cart', ['total']),
	...mapState('cart', ['cart']),
  },
  onShow() {
     // 在页面刚展示的时候，设置数字徽标
     this.setBadge()
	 
  },
  methods: {
	  ...mapMutations('cart', ['updateGoodsState','updateGoodsCount','removeGoodsById']),
	 
     setBadge() {
        // 调用 uni.setTabBarBadge() 方法，为购物车设置右上角的徽标
        uni.setTabBarBadge({
           index: 2, // 索引
           text: this.total + '' // 注意：text 的值必须是字符串，不能是数字
        })
     },
	 radioChangeHandler(e) {
	    this.updateGoodsState(e) // 输出得到的数据 -> {goods_id: 395, goods_state: false}
	   },
	   numberChangeHandler(e) {
	      this.updateGoodsCount(e)
	   },
	    swipeActionClickHandler(goods) {
	       this.removeGoodsById(goods.goods_id)
	     },
  }
}
</script>

<style lang="scss">
	.cart-container {
	  padding-bottom: 50px;
	}
.cart-title {
  height: 40px;
  display: flex;
  align-items: center;
  font-size: 14px;
  padding-left: 5px;
  border-bottom: 1px solid #efefef;
  .cart-title-text {
    margin-left: 10px;
  }
}
.goods-item {
  // 让 goods-item 项占满整个屏幕的宽度
  width: 750rpx;
  // 设置盒模型为 border-box
  box-sizing: border-box;
  display: flex;
  padding: 10px 5px;
  border-bottom: 1px solid #f0f0f0;
}
.empty-cart {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 150px;

  .empty-img {
    width: 90px;
    height: 90px;
  }

  .tip-text {
    font-size: 12px;
    color: gray;
    margin-top: 15px;
  }
}
</style>
